
const dataSource = [{
    key: '1',
    name: '',
    age: 32,
    address: '1'
}, {
    key: '2',
    name: '',
    age: 42,
    address: '1'
}];

const columns = [{
    title: '',
    dataIndex: 'name',
    key: 'name',
}, {
    title: '',
    dataIndex: 'age',
    key: 'age',
}, {
    title: '',
    dataIndex: 'address',
    key: 'address',
}];

import { DatePicker, message ,Table} from 'antd';
import {Link} from 'react-router'
const App = React.createClass({
    getInitialState() {
        return {
            date: ''
        };
    },
    handleChange(value) {
        message.info(': ' + value.toString());
        this.setState({
            date: value
        });
    },
    render() {
        return (
            <div style={{width: 400, margin: '0 auto 100px'}}>
                <DatePicker onChange={this.handleChange} />
                <div style={{marginTop: 20}}>{this.state.date.toString()}</div>
                <Table dataSource={dataSource} columns={columns} />
                <Link to={{pathname:'app2',query:{}}}>test</Link>
            </div>
        )
    }
});

export default App;


